.knowledges {
  //   display: flex;
  //   flex-direction: row;
  .knowledgesContent {
    min-height: $min-height;
    width: 100%;
    position: relative;
    overflow: hidden;
    display: grid;
    grid-template-columns: 38% 1fr 1fr;
    grid-template-rows: 70% 30%;
    grid-template-areas:
      "L E E"
      "O O H";
    .languagesFrameworks,
    .experience,
    .otherSkills,
    .hobbies {
      background: $color-2;
      border-radius: $borderRadius;
      margin: 10px;
      padding: 0.8rem;
      box-shadow: rgba(0, 0, 0, 0.17) 0px -23px 25px 0px inset,
        rgba(0, 0, 0, 0.15) 0px -36px 30px 0px inset,
        rgba(0, 0, 0, 0.1) 0px -79px 40px 0px inset,
        rgba(0, 0, 0, 0.06) 0px 2px 1px, rgba(0, 0, 0, 0.09) 0px 4px 2px,
        rgba(0, 0, 0, 0.09) 0px 8px 4px, rgba(0, 0, 0, 0.09) 0px 16px 8px,
        rgba(0, 0, 0, 0.09) 0px 32px 16px;
    }
    h3 {
      font-size: 1.4rem;
      margin-bottom: 1rem;
      font-family: $font-2;
      color: $color-5;
      text-transform: uppercase;
    }
    .languagesFrameworks {
      grid-area: L;
      margin-bottom: 0.5rem;
      display: grid;
      grid-template-columns: 1fr;
      grid-template-rows: 40% 60%;
      grid-template-areas:
        "A"
        "F";
      .languageDisplay {
        grid-area: A;
      }
      .frameworkDisplay {
        grid-area: F;
      }
      h3 {
        margin-bottom: 0.3rem;
      }
      .years {
        font-size: 0.65rem;
        display: flex;
        justify-content: space-between;
        color: $white;
        span {
          width: 36%;
          &:nth-child(2) {
            text-align: center;
            width: 20%;
          }
          &:nth-child(3) {
            text-align: right;
            width: 44%;
          }
          &:nth-child(2):before,
          &:nth-child(3):before {
            content: "";
            background: rgba(180, 179, 179, 0.5);
            position: absolute;
            height: 165px;
            width: 1px;
            transform: translate(9px, 19px);
          }
        }
      }
      .languagesList {
        padding-top: 0.5rem;
        li {
          color: $color-1;
        }
        .progressBar {
          height: 0.4rem;
          background: transparent;
          border-radius: 50px;
          margin-top: 0.2rem;
          position: relative;

          &:after {
            content: "";
            position: absolute;
            background: $color-3;
            border-radius: 50px;
            height: 0.4rem;
            width: 0;
            animation: line 2s ease-out forwards;
            @keyframes line {
              to {
                width: 100%;
              }
            }
          }
        }
      }
    }
    .experience {
      grid-area: E;
      // margin: 0 0 0.5rem 0.5rem;
      display: grid;
      grid-template-columns: 1fr;
      grid-template-rows: 10% 1fr 1fr 1fr 1fr;
      grid-area: "1" "2" "3" "4" "5";
      h3 {
        grid-area: 1;
      }
      h4 {
        font-size: 1.1rem;
        color: $color-5;
      }
      h5 {
        color: $color-3;
        padding: 0.5rem 0 0.5rem 1rem;
      }
      p {
        text-align: justify;
        padding: 0 1rem;
      }
      .exp-1 {
        grid-area: 2;
      }
      .exp-2 {
        grid-area: 3;
      }
      .exp-3 {
        grid-area: 4;
      }
      .exp-4 {
        grid-area: 5;
      }
      li {
        padding-bottom: 1.5rem;
        border-left: 1px solid #abaaed;
        position: relative;
        padding-left: 20px;
        margin-left: 10px;
        // &:last-child {
        //   border: 0px;
        //   padding-bottom: 0;
        // }
        &:before {
          content: "";
          width: 15px;
          height: 15px;
          background: white;
          border: 1px solid #4e5ed3;
          box-shadow: 3px 3px 0px #bab5f8;
          box-shadow: 3px 3px 0px #bab5f8;
          border-radius: 50%;
          position: absolute;
          left: -10px;
          top: 0px;
        }
      }
    }
    .otherSkills {
      grid-area: O;
      margin-right: 0.5rem;

      .list {
        display: flex;
        width: 80%;
        height: 100%;
        margin: 0 auto;
        justify-content: space-between;

        ul {
          height: 80%;
          column-count: 2; /* Divise la liste en deux colonnes */
          column-gap: 1rem; /* Espace entre les colonnes */
          .listIcon {
            color: $color-5;
            margin-right: 15px;
          }
          li {
            padding: 10px;
          }
        }
      }
    }
    .hobbies {
      grid-area: H;
      padding-left: 1.5rem;

      ul {
        height: 80%;
        display: grid;

        li {
          .iconHobby {
            position: absolute;
            color: $color-5;
          }
          span {
            position: absolute;
            transform: translateX(2.2rem);
          }
        }
      }
    }
  }
}
@media screen and (max-width: 950px) {
  .knowledgesContent {
    h3 {
      font-size: 1, 2rem !important;
    }
    .languagesList {
      padding-top: 0.2rem !important;
    }
    p {
      font-size: 0.8rem;
    }
  }
}
@media screen and (max-width: 850px) {
  .knowledgesContent {
    h4 {
      font-size: 0.9rem !important;
    }
    h5 {
      font-size: 0.6rem !important;
      // padding: 2rem 1rem !important;
    }
  }
}
@media screen and (max-width: 500px) {
  .knowledgesContent {
    display: grid !important;
    grid-template-columns: 1fr !important;
    grid-template-rows: 1fr 1fr 250px 190px !important;
    grid-template-areas:
      "1"
      "2"
      "3"
      "4" !important;

    .languagesFrameworks {
      grid-area: 1 !important;
    }

    .experience {
      grid-area: 2 !important;
      p {
        margin-bottom: 10px !important;
      }
    }

    .otherSkills {
      grid-area: 3 !important;
      margin-left: 0 !important;
    }

    .hobbies {
      grid-area: 4 !important;
      margin-top: 0.5rem !important;
    }
  }
}
